.wp-block-adelcourte-image-caption-content {
    max-width: 960px !important;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 32px;

    .left, .right {
        position: relative;
    }

    .left {
        grid-column: span 3;
    }

    .right {
        grid-column: span 2;
    }

    .caption {
        position: absolute;
        left: 16px;
        bottom: 8px;
        color: white;
        text-shadow: #00000066 -2px 2px 20px;
        z-index: 15;
        font-size: 14px;
        text-transform: uppercase;
        font-weight: 300;
        margin: 0;
    }

    .image-wrapper {
        position: relative;
        aspect-ratio: 4/3;
        width: 100%;

        img {
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            max-width: none;
            max-height: none;
            z-index: 5;
        }

        .overlay {
            position: absolute;
            inset: 75% 0 0 0;
            z-index: 10;
            background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.5));
            content: "";
        }
    }

    .content {
        p {
            margin-top: 0;
        }
    }
}